import React from 'react';
import { Container , Row ,Col} from 'react-bootstrap';
import colors from '../assets/colors';

class Footer extends React.Component{
    constructor(props){
        super(props);
        this.state={}
    }
    render(){
        return(
            <div className="Footer">
                <div className="FooterContainer">
                    <Row style={{display:"flex",justifyContent:"center",alignItems:"center",margin:0,marginBottom:15,width:"100%"}}>
                        <img src={require('../assets/images/logoFooter.png')} width="40" style={{cursor:"pointer"}} />
                    </Row>
                    <div className="" style={{display:"flex",justifyContent:"center",alignItems:"center",marginBottom:15}}>
                        <Container style={{display:"flex",justifyContent:"center",alignItems:"center"}}>
                            <ul className="FooterItemUl">
                                <li className="FT2" style={{fontFamily:global.font().HeeboRegular}}>Hayatemoon magazine</li>
                                <li className="FT2" style={{fontFamily:global.font().HeeboRegular}}>Terms of service</li>
                                <li className="FT2" style={{fontFamily:global.font().HeeboRegular}}>Privacy policy</li>
                                <li className="FT2" style={{fontFamily:global.font().HeeboRegular}}>Support</li>
                                <li className="FT2" style={{fontFamily:global.font().HeeboRegular}}>About</li>
                            </ul>
                        </Container>
                    </div>
                    <Container>
                        <Row style={{display:"flex",justifyContent:"space-between",alignItems:"center",width:"100%",margin:0,marginBottom:15}}>
                            <Col lg={3} style={{display:"flex",justifyContent:"center",alignItems:"center",marginBottom:15}}><small style={{fontFamily:global.font().HeeboRegular,color:colors.gray}}>English</small></Col>
                            <Col lg={6} className="FootersViewSocial" style={{marginBottom:15}}>
                                <div style={{display:"flex",justifyContent:"center",alignItems:"center"}}>
                                    <img style={{marginRight:35}} src={require('../assets/icons/telegram.svg')} width="30"/>
                                    <img style={{marginRight:35}} src={require('../assets/icons/instagram.svg')} width="30"/>
                                    <img src={require('../assets/icons/mail.svg')} width="30"/>
                                </div>
                            </Col>
                            <Col lg={3} style={{display:"flex",justifyContent:"center",alignItems:"center",marginBottom:15}}><small style={{fontFamily:global.font().HeeboRegular,color:colors.gray}}>©2020 Hayatemoon All-right reserved</small></Col>
                            
                            
                            
                        </Row>
                    </Container>
                </div>
            </div>
        )
    }
}

export default Footer;